import banner from "@/components/banner/banner.vue"
import contact from "@/components/contact/contact.vue"
import searchInput from "@/components/bm-searchInput/bm-searchInput.vue"
import uniIcons from "@/components/uni-icons/uni-icons.vue";
// import uniSwipeAction from "@dcloudio/uni-ui"
import uniSwipeAction from '@/components/uni-swipe-action/uni-swipe-action.vue'
import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
// import uniSwipeAction from "@dcloudio/uni-ui"
export default {
	components:{searchInput,uniIcons,uniSwipeAction,uniList,uniListItem,banner,contact},
	data() {
		return {
			title: 'Hello',
			isOpened: false,
			history:[],
			options1: [{
				text: '取消置顶'
			}],
			options2: [{
				text: '删除',
				style: {
					backgroundColor: '#dd524d'
				}
			}],
			options3: [{
				text: '置顶'
			}, {
				text: '标记为已读',
				style: {
					backgroundColor: 'rgb(254,156,1)'
				}
			}, {
				text: '删除',
				style: {
					backgroundColor: 'rgb(255,58,49)'
				}
			}]
		}
	},
	onLoad() {
		this.$nextTick(() => {
			this.isOpened = true
		})
		this.getHistory();
	},
	methods: {
		filterList(list){
			_this.history = list;
		},
		toUrl(url){
			uni.navigateTo({
				"url":url
			})
		},
		tcm(s){
			uni.navigateTo({
				"url":"/pages/address/mszc/tcm?qrcode="+s.qrcodeNo
			})
		},
		getHistory(){
			let _this = this;
			this.$ajax("/park/page","POST",{"pageNo":1,"pageSize":3}).then((res)=>{
				console.log(res);
				_this.history = res.list;
			})
		},
		openMap(s){
			let _this = this;
			console.log(s);
			uni.openLocation({
			    latitude: parseFloat(s.longitude),
			    longitude: parseFloat(s.latitude),
			    success: function () {
			        console.log('success');
			    }
			});
		},
		bindClick(e,s) {
			let _this = this;
			if(e.content.text == '删除'){
				this.$ajax("/park/remove","POST",{qrcodeNo:s.qrcodeNo}).then(res=>{
					if(res.code == '200'){
						uni.showToast({
							"title":res.msg,
							"icon":"none",
							"mask":"true"
						})
						setTimeout(function(){
							_this.getHistory();
						},999)
						
					}
				})
			}
			
		},
	}
}
